export default {
	name: 'getcode',
	props:{
		orderNum: Number,
		isDefault:{
			type: Boolean,
			default:true
		}
	},
	data() {
		return {
			isShowTime: false,
			disable: false,
			title: '验证码：',
			titleSin: '验证码',
			timer: '60',
			codeText: '',
			placeholder: '请输入验证码',
			text: '获取验证码',
			s: '',
			id: 1,
			setTime: null
		}
	},
	created(e) {
		this.countDown(this)
		this.setInterValFunc(this)
	},
	beforeDestroy() {
		clearInterval(this.setTime)
	},
	methods: {
		// bindToast(){
			
		// },
		setInterValFunc(obj) {
			this.setTime = setInterval(() => {
				obj.countDown(obj)
			}, 1000)
		},
		click() {
			this.requsetUrl()
			this.isShowTime = true
			this.text = '重新获取'
			this.s = 60
		},
		countDown(self) {
			if (self.text == '重新获取') {
				if (self.s > 0) {
					self.s -= 1
				} else if (self.s == 0) {
					this.isShowTime = false
					self.s = 0
					self.text = '获取验证码'
				}
			}
		},
		onCode(e){
			let scode = e.detail.value
			this.$emit('scode', {scode})
		},
		requsetUrl(){
			let orderNum = this.orderNum
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/getNumber',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					phone: orderNum
				},
				success: res => {
					console.log(res)
					let phoneCode = res.data.data
					this.$emit('phoneCode', {phoneCode})
				},
				fail:err=>{
					console.log(err)
				}
			})
		}
	}
}
